import * as actionTypes from '../actions/projectActions';

const initialState = {
  sideDrawerOpen: false
};

const modal = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.TOGGLE_SIDE_DRAWER:
      return {
        sideDrawerOpen: !state.sideDrawerOpen
      };

    default:
      return state;
  }
};

export default modal;
