import * as actionTypes from '../actions/modalActions';

const initialState = {
  checkOutModal: false,
  sideBarOpen: false,
  navigationBarOpen: false
};

const modal = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.TOGGLE_CHECKOUT_MODAL:
      return {
        ...state,
        checkOutModal: !state.checkOutModal
      };
    case actionTypes.TOGGLE_SIDEBAR:
      return {
        ...state,
        sideBarOpen: !state.sideBarOpen
      };
    case actionTypes.TOGGLE_NAVIGATIONBAR:
      return {
        ...state,
        navigationBarOpen: !state.navigationBarOpen
      };

    default:
      return state;
  }
};

export default modal;
