import * as actionTypes from '../actions/checkOutActions';

const initialState = {
  checkOutItems: [
    {
      colorOptions: ['#f5ef42', '#f54242', '#42e3f5'],
      productImage: 'url(https://source.unsplash.com/pO2bglTMJpo)',
      productName: 'Product Name',
      productPrice: '44',
      productManufacturer: 'Manufacturer3',
      productSize: 'XXL',
      productQuantity: '2',
      id: 1
    },
    {
      colorOptions: ['#f5ef42', '#f54242', '#42e3f5'],
      productImage: 'url(https://source.unsplash.com/pO2bglTMJpo)',
      productName: 'Product Name 2',
      productPrice: '44',
      productManufacturer: 'Manufacturer3',
      productSize: 'XXL',
      productQuantity: '2',
      id: 2
    },
    {
      colorOptions: ['#f5ef42', '#f54242', '#42e3f5'],
      productImage: 'url(https://source.unsplash.com/pO2bglTMJpo)',
      productName: 'Product Name 3',
      productPrice: '44',
      productManufacturer: 'Manufacturer3',
      productSize: 'XXL',
      productQuantity: '2',
      id: 3
    }
  ],
  currentItem: {
    productImage: '',
    productName: '',
    productPrice: '',
    productManufacturer: '',
    productSize: '',
    productQuantity: 1,
    productColor: '',
    id: null
  }
};

const shoppingChart = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.ADD_ITEM_TO_CHECKOUT:
      return {
        ...state,
        checkOutItems: state.checkOutItems.concat(state.currentItem)
      };
    case actionTypes.UPDATE_CURRENT_ITEM:
      return {
        ...state,
        currentItem: {
          ...state.currentItem,
          [action.fieldName]: action.value
        }
      };
    case actionTypes.REMOVE_ITEM_FROM_CHECKOUT:
      return {
        ...state,
        checkOutItems: state.checkOutItems.filter(
          (item, index) => index !== action.value
        )
      };

    default:
      return state;
  }
};

export default shoppingChart;
