import * as actionTypes from '../actions/filterActions';
import * as _ from 'lodash';
import ProductTopHeader from '../../components/ProductCollection/ProductTopHeader';

const initialState = {
  size: [],
  color: [],
  category: [],
  brand: [],
  rating: [],
  price: [],
  sale: false,
  madeInCanada: false,
  tag: [],
  productList: [],
  filteredProductList: []
};

const filter = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_FILTER_CRITERIA:
      return {
        ...state,
        [action.value.filterName]: action.value.filterValue,

        filteredProductList: action.value.filterValue.length
          ? _.filter(state.productList, function(num) {
              return (
                action.value.filterValue.includes(num.manufacturer) ||
                action.value.filterName === 'size'
              );
            })
          : state.productList
      };
    case actionTypes.REMOVE_CRITERIA_FROM_FILTER:
      const updatedList = state[action.filterName].filter(
        filterName => filterName !== action.value
      );
      return {
        ...state,
        [action.filterName]: updatedList,
        filteredProductList: updatedList.length
          ? _.filter(state.productList, function(num) {
              return (
                updatedList.includes(num.manufacturer) ||
                action.filterName === 'size'
              );
            })
          : state.productList
      };

    case actionTypes.SET_PRODUCT_LIST:
      return {
        ...state,
        productList: action.value,
        filteredProductList: action.value
      };

    case actionTypes.SORT_BY_FEATURED:
      return {
        ...state,
        productList: _.shuffle(state.productList)
      };
    case actionTypes.SORT_BY_HIGHEST_PRICE:
      return {
        ...state,
        productList: _.orderBy(state.productList, ['price'], ['desc']),
        filteredProductList: _.orderBy(
          state.filteredProductList,
          ['price'],
          ['desc']
        )
      };
    case actionTypes.SORT_BY_LOWEST_PRICE:
      return {
        ...state,
        productList: _.orderBy(state.productList, ['price'], ['asc']),
        filteredProductList: _.orderBy(
          state.filteredProductList,
          ['price'],
          ['asc']
        )
      };
    case actionTypes.SORT_BY_HIGHEST_RATE:
      return {
        ...state,
        productList: _.orderBy(state.productList, ['rate'], ['desc']),
        filteredProductList: _.orderBy(
          state.filteredProductList,
          ['rate'],
          ['desc']
        )
      };
    case actionTypes.SORT_BY_DISCOUNT:
      return {
        ...state,
        productList: _.orderBy(state.productList, ['discount'], ['desc']),
        filteredProductList: _.orderBy(
          state.filteredProductList,
          ['discount'],
          ['desc']
        )
      };

    default:
      return state;
  }
};

export default filter;
